
import { useState } from "react";
import { dbService, storageService } from "fbase";
import { v4 as uuidv4 } from "uuid";


const TweetFactory = ({userObj}) => {
    const [weet, setWeet] = useState(""); // eslint-disable-line no-unused-vars
    const [attachment, setAttachment] = useState("");

    const onSubmit = async (event) => {
        event.preventDefault();

        let attachmentUrl = "";
        if (attachment !== "") {
            const attachmentRef = storageService
                .ref()
                .child(`${userObj.uid}/${uuidv4()}`);
            const response = await attachmentRef.putString(attachment, "data_url");
            attachmentUrl = await response.ref.getDownloadURL();
            //console.log(await response.ref.getDownloadURL());
        }

        await dbService.collection("weets").add({
            text: weet,
            createdAt: Date.now(),
            creatorId: userObj.uid,
            attachmentUrl,
        });

        setWeet("");
        setAttachment("");
    };

    const onChange = (event) => {
        event.preventDefault();
        const {
            target: {value},
        }= event;
        setWeet(value);
    };

    const onFileChange = (event) => {
        //console.log(event.target.files);
        const {
            target: {files},
        } = event;
        const theFile = files[0]; // eslint-disable-line no-unused-vars
        const reader = new FileReader(); // eslint-disable-line no-unused-vars
        reader.onloadend = (finishedEvent) => {
            //console.log(finishedEvent);
            const {
                currentTarget: {result},
            } = finishedEvent;
            setAttachment(result);
        }
        reader.readAsDataURL(theFile);
    };

    const onClearAttachment = () => setAttachment("");

    return (
        <>
            <form onSubmit={onSubmit}>
                <input
                    value={weet}
                    onChange={onChange}
                    type="text"
                    placeholder="What's on your mind?"
                    maxLength={120}
                />
                <input type="file" accept="image/*" onChange={onFileChange}/>
                <input type="submit" value="Tweet" />
                {attachment && (
                    <div>
                        <img src={attachment} width="50px" height="50px" />  
                        <button onClick={onClearAttachment}>Clear</button>
                    </div>
                )}
            </form>
        </>
    );
};


export default TweetFactory;