import { useState, useEffect } from "react";
import { dbService, storageService } from "fbase";
import Weet from "components/Tweet"
import { v4 as uuidv4 } from "uuid";
import TweetFactory from "components/TweetFactory";

const Home = ( {userObj} ) => {

    const [weet, setWeet] = useState(""); // eslint-disable-line no-unused-vars
    const [weets, setWeets] = useState([]); // eslint-disable-line no-unused-vars
    const [attachment, setAttachment] = useState("");

    /* 실시간 데이터베이스로 목록 보여주기 위해 삭제
    const getWeets = async () => {
        const dbWeets = await dbService.collection("weets").get();
        dbWeets.forEach((document) => {
            const weetObject = { ...document.data(), id: document.id };
            setWeets((prev) => [weetObject, ...prev])
        });
    };
    */

    useEffect( () => {
        // 실시간 데이터베이스로 목록 보여주기 위해 삭제
        //getWeets();

        dbService.collection("weets").onSnapshot((snapshot) => {
            const newArray = snapshot.docs.map(
                (document) => ({
                    id: document.id,
                    ...document.data(),
                })
            );
            setWeets(newArray);
        });
    }, []);

    return (
        <>
            <TweetFactory userObj={userObj}/>
            <div>
                {weets.map((weet) => (
                    //<div key={weet.id}>
                    //    <h4>{weet.text}</h4>
                    //</div>
                    <Weet 
                        key =  {weet.id} 
                        weetObj={weet} 
                        isOwner={weet.creatorId === userObj.uid}/>
                ))}
            </div>
        </>
    );
};

export default Home;